<template>
  <!-- vue3.0入门 -->
  <div id="app">
    <audio :src="audioUrl" ref="audioRef"  loop></audio>
    <div class="audio-sign border-rounded" @click="audioPlayHandler">
      <img
        :class="isRotate ? 'audio-active' : ''"
        style="width:30px;height: 30px;" 
        src="./assets/images/audio-flag.png">
    </div>
    <router-view></router-view>
    <Footer v-if="isNavShow"></Footer>
  </div>
</template>
<script>
import AudioCom from './components/AudioCom'

import Footer from './components/Footer'
import {mapState} from 'vuex'
export default {
  computed:mapState(['isNavShow']), //数组的写法
  components:{
    Footer,
    AudioCom
  },
  data() {
    return {
      audioUrl: require("./assets/audio/married.mp3"),
      isPlay: true,
      isRotate: false
    }
  },
  mounted (){ 
    setTimeout(()=>{
      this.isRotate = true;
    },2000)
    this.$refs['audioRef'].play()
    this.$store.dispatch('hideNav') //初始化显示这个tabbar
  },
  methods: {
    audioPlayHandler() {
      console.log("点击")
      if (this.isPlay) {
          this.$refs['audioRef'].pause();
          this.isPlay = false
          this.isRotate = false;
        } else {
          this.$refs['audioRef'].play();
          this.isPlay = true;
          this.isRotate = true;
        }
    }
  }
}
</script>
<style scoped>
  @import url("./assets/common.css");
  @import url("./assets/free.css");
  .audio-sign {
    position: fixed;width: 50px; height:50px;top:60px; right:15px;background-color: rgba(0,0,0,0.4);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    transition:all infinite ease ;
  }
  .audio-active {
    animation: rotation 4s linear infinite ;
  }
  @keyframes rotation{
    from {transform: rotate(0deg);}
    to {transform: rotate(360deg);}
  }
</style>
