<template>
  <div class="flex a-center j-center pt-05 pb-2">
    <img v-if="loadmore=='loading'"
     class="mr-2"
    src="../assets/images/loading.gif" style="width: 60rpx;height: 60rpx;"></img>
    <text class="fz-28 cor-9d9 fw-4"
    >{{loadText}}</text>
  </div>
</template>

<script>
  export default {
    name:"load-more",
    props: {
      obj: {
        type: Object,
        default: ()=>{
         return {
           loadmore: '上拉加载更多',
           loading: '加载中...',
           nomore: "没有更多了哦~"
         }
        }
      },
      loadmore: {
        type: String,
        default: 'loadmore'
      }
    },
    computed: {
      loadText() {
        return this.obj[this.loadmore]
      }
    },
    data() {
      return {
        
      };
    }
  }
</script>

