<template>
  <div class="d-flex overflow-hidden h-250 p-relative">
    <swiper :options="swiperOption" ref="mySwiper">
      <swiper-slide class="w-percent100 h-260 " v-for="(item,index) in list" :key="index">
        <img class="d-block w-percent100 h-percent100" :src="item.img" @click.stop="jumpBanana(item)">
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>

  </div>
</template>

<script>
// toRefs是响应式的数据，是一个方法
//传的时候是父组件中的provide 接收子组件中的是inject
export default {
  data() {
    return {
      activeSlide: 0, //获取当前的索引值
      swiperOption: {
        initialSlide: 0, //初始化索引
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: true,
          bulletClass: 'my-bullet',// 一般
          bulletActiveClass: 'my-bullet-active',// 选中
        },
        on: {
          slideChange() {},
        },
        spaceBetween: 30,
        //自动轮播
        autoplay: {
          delay: 3000,
          //当用户滑动图片后继续自动轮播
          disableOnInteraction: false,
        },
      },
      list: [
        {
          id: 1,
          img: require('../assets/images/banner-1.png'),
        },
        {
          id: 2,
          img: require('../assets/images/banner-2.png'),
        },
      ],
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    },
  },
  methods: {
    jumpBanana(item) {
      this.$router.push({
        path: 'banana',
        query: item
      })
    }
  },
  mounted() {},
}
</script>

<style scoped>
  .swiper-pagination{
    padding-bottom: 5px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .swiper-pagination /deep/ .my-bullet{
    width: 10px;
    height: 10px;
    border-radius: 5px;
    margin-left: 14px;
    background: #e0e5fe;
    display: block;
  }
  .swiper-pagination /deep/ .my-bullet-active{
    width: 30px;
    height: 10px;
    border-radius: 5px;
    margin-left: 10px;
    display: block;
    background: #ffffff;
  }
</style>
